@import url(https://fonts.googleapis.com/css2?family=Holtwood+One+SC&family=Space+Mono:wght@400;700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #CBA18C;
  font-family: 'Space Mono', monospace;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.headers {
  font-family: 'Holtwood One SC', serif;
}

header h1 {
  font-size: 20px;
  font-weight: bolder;
  width: 45%;
}

#res-header {
  text-align: center;
  color: #516F76;
  margin-top: 50px;
}

.bg-opacity {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
  color: white;
  border-radius: 15px;
  padding: 7px;
  cursor: auto;
}

#summary-bg {
  background-image: url(/the-prompt-shop/static/media/andrea-de-santis-zwd435-ewb4-unsplash.47e3460f.jpg);
  background-size: cover;
  height: 100vh;
}

.sum-box{
  width: 420px;
  margin: 0 auto;
  margin-top: 20px;
  padding-bottom: 10px;
}

#sum {
  width: 400px;
  margin: 0 auto;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 700;
  padding: 10px;
  cursor: auto;
}

#sum a {
  text-decoration: none;
  color: white;
  background-color: #874B41;
  padding: 5px;
  padding-top: 1px;
  border-radius: 10px;
}

textarea {
  display: block;
  resize: none;
  margin: 0 auto;
  margin-bottom: 10px;
  margin-top: 80px;
  padding: 5px;
  border-radius: 5px;
}

.gen-btn {
  background-color: #516F76;
  color: white;
  display: block;
  margin: 0 auto;
  border-style: none;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 10px;
}

#clear{
  background-color: #874B41;
}

.error-text {
  color: white;
  background-color: red;
  margin: 0 auto;
  margin-bottom: 20px;
  width: 60%;
  text-align: center;
  border-radius: 5px;
  padding: 5px;
}

.prompt-card {
  border-style: solid;
  border-color: #516F76;
  border-width: 5px;
  background-color: #874B41;
  color: white;
  width: 700px;
  margin: 0 auto;
  margin-top: 30px;
}

#prompt {
  overflow-y: hidden;
}

.res-scroll {
  height: 100%;
  overflow-y: scroll;
}

.row-style {
  height: 100vh;
}

#response {
  font-weight: 900;
  font-size: 20px;
}


